import React from "react"
import { graphql, PageProps } from "gatsby"
import { Article, Sparkle } from "@phosphor-icons/react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import { FeaturedPostCard, ArticleListItem } from "../components/blog"
import { Hero } from "../components/home"
import IconWrapper from "../components/icons/IconWrapper"

interface PostNode {
  node: {
    excerpt: string
    fields: {
      slug: string
    }
    frontmatter: {
      date: string
      title: string
      description?: string
      tags?: string[]
    }
  }
}

interface IndexPageData {
  site: {
    siteMetadata: {
      title: string
    }
  }
  allMarkdownRemark: {
    edges: PostNode[]
  }
}

const BlogIndex: React.FC<PageProps<IndexPageData>> = ({
  data,
  location,
}) => {
  const siteTitle = data.site.siteMetadata.title
  const posts = data.allMarkdownRemark.edges

  // Split posts: first 1-2 for featured cards, rest for list
  const featuredPosts = posts.slice(0, 2)
  const listPosts = posts.slice(2)

  return (
    <Layout location={location} title={siteTitle}>
      <SEO
        title="All posts"
        keywords={[`blog`, `gatsby`, `javascript`, `react`]}
      />

      {/* Hero Section with Terminal Effect */}
      <Hero />

      {/* Featured Posts Section */}
      {featuredPosts.length > 0 && (
        <section className="mb-12">
          {/* Section Header */}
          <div className="flex items-center gap-2 mb-5 fade-in">
            <IconWrapper
              icon={Sparkle}
              size={20}
              className="text-accent"
              weight="duotone"
            />
            <h2 className="text-lg font-bold text-foreground font-serif">
              最新文章
            </h2>
          </div>

          {/* Featured Cards Grid */}
          <div
            className={`grid gap-6 ${
              featuredPosts.length === 1
                ? "grid-cols-1 max-w-2xl"
                : "grid-cols-1 md:grid-cols-2"
            }`}
          >
            {featuredPosts.map(({ node }, index) => {
              const title = node.frontmatter.title || node.fields.slug
              return (
                <FeaturedPostCard
                  key={node.fields.slug}
                  slug={node.fields.slug}
                  title={title}
                  date={node.frontmatter.date}
                  description={node.frontmatter.description}
                  excerpt={node.excerpt}
                  tags={node.frontmatter.tags}
                  index={index}
                />
              )
            })}
          </div>
        </section>
      )}

      {/* Article List Section */}
      <section>
        {/* Section Header */}
        <div className="flex items-center gap-2 mb-5 fade-in">
          <IconWrapper
            icon={Article}
            size={20}
            className="text-accent"
            weight="duotone"
          />
          <h2 className="text-lg font-bold text-foreground font-serif">
            {featuredPosts.length > 0 ? "更多文章" : "最新文章"}
          </h2>
          {listPosts.length > 0 && (
            <span className="ml-2 text-xs text-foreground-secondary">
              ({listPosts.length} 篇)
            </span>
          )}
        </div>

        {/* Articles List */}
        {posts.length === 0 ? (
          /* Empty State */
          <div className="text-center py-16 px-4 bg-background-card rounded-2xl fade-in">
            <div className="flex justify-center mb-4">
              <div className="w-14 h-14 rounded-full bg-accent-subtle/50 flex items-center justify-center">
                <IconWrapper
                  icon={Article}
                  size={28}
                  className="text-accent"
                  weight="duotone"
                />
              </div>
            </div>
            <h3 className="text-base font-medium text-foreground mb-2">
              暂无文章
            </h3>
            <p className="text-sm text-foreground-muted">
              精彩内容正在筹备中，敬请期待...
            </p>
          </div>
        ) : listPosts.length === 0 ? (
          /* No more articles message when only featured exist */
          <div className="text-center py-12 text-sm text-foreground-muted fade-in">
            <p>更多文章即将发布...</p>
          </div>
        ) : (
          /* Article List - 使用色块区分，无边框 */
          <div className="bg-background-card rounded-2xl p-3 sm:p-4">
            {listPosts.map(({ node }, index) => {
              const title = node.frontmatter.title || node.fields.slug
              return (
                <ArticleListItem
                  key={node.fields.slug}
                  slug={node.fields.slug}
                  title={title}
                  date={node.frontmatter.date}
                  description={node.frontmatter.description}
                  excerpt={node.excerpt}
                  tags={node.frontmatter.tags}
                  index={index}
                />
              )
            })}
          </div>
        )}
      </section>
    </Layout>
  )
}

export default BlogIndex

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(
      sort: { frontmatter: { date: DESC } }
      filter: { frontmatter: { published: { ne: false } } }
    ) {
      edges {
        node {
          excerpt
          fields {
            slug
          }
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            title
            description
            tags
          }
        }
      }
    }
  }
`
