import React, { useEffect, useRef, useState } from "react"

interface TypeWriterOptions {
  text: string
  delay?: number
  onComplete?: () => void
}

const useTypeWriter = (options: TypeWriterOptions) => {
  const { text, delay = 50, onComplete } = options
  const [displayText, setDisplayText] = useState("")
  const [isComplete, setIsComplete] = useState(false)
  const indexRef = useRef(0)
  const timeoutRef = useRef<NodeJS.Timeout | null>(null)

  useEffect(() => {
    setDisplayText("")
    setIsComplete(false)
    indexRef.current = 0

    const typeChar = () => {
      if (indexRef.current < text.length) {
        setDisplayText(text.slice(0, indexRef.current + 1))
        indexRef.current++
        timeoutRef.current = setTimeout(typeChar, delay)
      } else {
        setIsComplete(true)
        onComplete?.()
      }
    }

    // Start typing after a small initial delay
    timeoutRef.current = setTimeout(typeChar, 300)

    return () => {
      if (timeoutRef.current) {
        clearTimeout(timeoutRef.current)
      }
    }
  }, [text, delay, onComplete])

  return { displayText, isComplete }
}

interface TypingLineProps {
  text: string
  prefix?: string
  className?: string
  delay?: number
  showCursor?: boolean
  onComplete?: () => void
}

const TypingLine: React.FC<TypingLineProps> = ({
  text,
  prefix = "",
  className = "",
  delay = 40,
  showCursor = true,
  onComplete,
}) => {
  const { displayText, isComplete } = useTypeWriter({ text, delay, onComplete })

  return (
    <span className={className}>
      {prefix}
      {displayText}
      {showCursor && (
        <span
          className={`inline-block w-[0.6em] h-[1.1em] bg-primary ml-1 align-middle ${
            isComplete ? "animate-blink" : ""
          }`}
          style={{ opacity: isComplete ? undefined : 1 }}
        />
      )}
    </span>
  )
}

const AnimatedBackground: React.FC = () => {
  const canvasRef = useRef<HTMLCanvasElement>(null)
  const animationRef = useRef<number | null>(null)
  const dotsRef = useRef<{ x: number; y: number; vx: number; vy: number }[]>([])

  useEffect(() => {
    const canvas = canvasRef.current
    if (!canvas) return

    const ctx = canvas.getContext("2d")
    if (!ctx) return

    const resizeCanvas = () => {
      const rect = canvas.parentElement?.getBoundingClientRect()
      if (rect) {
        canvas.width = rect.width
        canvas.height = rect.height
      }
    }

    const initDots = () => {
      const dots: { x: number; y: number; vx: number; vy: number }[] = []
      const numDots = Math.min(25, Math.floor((canvas.width * canvas.height) / 25000))

      for (let i = 0; i < numDots; i++) {
        dots.push({
          x: Math.random() * canvas.width,
          y: Math.random() * canvas.height,
          vx: (Math.random() - 0.5) * 0.3,
          vy: (Math.random() - 0.5) * 0.3,
        })
      }
      dotsRef.current = dots
    }

    const drawGrid = () => {
      const isDark = document.documentElement.getAttribute("data-theme") === "dark"
      ctx.strokeStyle = isDark ? "rgba(52, 211, 153, 0.05)" : "rgba(16, 185, 129, 0.05)"
      ctx.lineWidth = 1

      const gridSize = 40
      for (let x = 0; x <= canvas.width; x += gridSize) {
        ctx.beginPath()
        ctx.moveTo(x, 0)
        ctx.lineTo(x, canvas.height)
        ctx.stroke()
      }
      for (let y = 0; y <= canvas.height; y += gridSize) {
        ctx.beginPath()
        ctx.moveTo(0, y)
        ctx.lineTo(canvas.width, y)
        ctx.stroke()
      }
    }

    const drawDots = () => {
      const isDark = document.documentElement.getAttribute("data-theme") === "dark"
      const dots = dotsRef.current

      // Draw dots
      dots.forEach((dot) => {
        ctx.beginPath()
        ctx.arc(dot.x, dot.y, 2, 0, Math.PI * 2)
        ctx.fillStyle = isDark ? "rgba(52, 211, 153, 0.3)" : "rgba(16, 185, 129, 0.3)"
        ctx.fill()
      })

      // Draw connections
      ctx.strokeStyle = isDark ? "rgba(52, 211, 153, 0.1)" : "rgba(16, 185, 129, 0.1)"
      ctx.lineWidth = 0.5

      for (let i = 0; i < dots.length; i++) {
        for (let j = i + 1; j < dots.length; j++) {
          const dx = dots[i].x - dots[j].x
          const dy = dots[i].y - dots[j].y
          const dist = Math.sqrt(dx * dx + dy * dy)

          if (dist < 100) {
            ctx.beginPath()
            ctx.moveTo(dots[i].x, dots[i].y)
            ctx.lineTo(dots[j].x, dots[j].y)
            ctx.stroke()
          }
        }
      }
    }

    const updateDots = () => {
      const dots = dotsRef.current
      dots.forEach((dot) => {
        dot.x += dot.vx
        dot.y += dot.vy

        if (dot.x < 0 || dot.x > canvas.width) dot.vx *= -1
        if (dot.y < 0 || dot.y > canvas.height) dot.vy *= -1
      })
    }

    const animate = () => {
      ctx.clearRect(0, 0, canvas.width, canvas.height)
      drawGrid()
      drawDots()
      updateDots()
      animationRef.current = requestAnimationFrame(animate)
    }

    resizeCanvas()
    initDots()
    animate()

    const handleResize = () => {
      resizeCanvas()
      initDots()
    }

    window.addEventListener("resize", handleResize)

    return () => {
      window.removeEventListener("resize", handleResize)
      if (animationRef.current) {
        cancelAnimationFrame(animationRef.current)
      }
    }
  }, [])

  return (
    <canvas
      ref={canvasRef}
      className="absolute inset-0 pointer-events-none"
      style={{ opacity: 0.6 }}
    />
  )
}

const Hero: React.FC = () => {
  const [currentLine, setCurrentLine] = useState(0)
  const [showSkills, setShowSkills] = useState(false)
  const [currentSkillIndex, setCurrentSkillIndex] = useState(0)
  const skillCycleRef = useRef<NodeJS.Timeout | null>(null)

  const introText = "Hi, I'm a full-stack developer passionate about building elegant solutions."

  const skills = [
    "TypeScript",
    "React",
    "Node.js",
    "GraphQL",
    "Cloud Architecture",
    "Open Source",
  ]

  useEffect(() => {
    if (showSkills) {
      skillCycleRef.current = setInterval(() => {
        setCurrentSkillIndex((prev) => (prev + 1) % skills.length)
      }, 2500)
    }

    return () => {
      if (skillCycleRef.current) {
        clearInterval(skillCycleRef.current)
      }
    }
  }, [showSkills, skills.length])

  return (
    <section className="relative w-full min-h-[400px] flex items-center justify-center py-16 px-4 overflow-hidden">
      {/* Animated Background */}
      <AnimatedBackground />

      {/* Terminal Container */}
      <div className="relative w-full max-w-3xl mx-auto">
        {/* Terminal Window */}
        <div
          className="rounded-xl overflow-hidden shadow-xl border border-border bg-background-card/95 backdrop-blur-sm"
          style={{
            boxShadow: "var(--shadow-xl)",
          }}
        >
          {/* Terminal Header */}
          <div className="flex items-center gap-2 px-4 py-3 bg-background-secondary border-b border-border">
            <div className="w-3 h-3 rounded-full bg-[#ff5f56]" />
            <div className="w-3 h-3 rounded-full bg-[#ffbd2e]" />
            <div className="w-3 h-3 rounded-full bg-[#27c93f]" />
            <span className="ml-4 text-xs text-foreground-muted font-mono">
              ~/welcome — zsh
            </span>
          </div>

          {/* Terminal Content */}
          <div className="p-6 sm:p-8 font-mono text-sm sm:text-base leading-relaxed min-h-[200px]">
            {/* Line 1: Command */}
            <div className="mb-4">
              <span className="text-primary">➜</span>
              <span className="text-foreground-muted ml-2">~</span>
              <span className="text-foreground ml-2">cat intro.txt</span>
            </div>

            {/* Line 2: Intro with typing effect */}
            {currentLine >= 0 && (
              <div className="mb-4 text-foreground-secondary">
                <TypingLine
                  text={introText}
                  delay={35}
                  onComplete={() => setCurrentLine(1)}
                />
              </div>
            )}

            {/* Line 3: Empty line */}
            {currentLine >= 1 && (
              <div className="h-4 fade-in" style={{ animationDelay: "200ms" }} />
            )}

            {/* Line 4: Skills command */}
            {currentLine >= 1 && (
              <div
                className="mb-4 fade-in"
                style={{ animationDelay: "400ms" }}
              >
                <span className="text-primary">➜</span>
                <span className="text-foreground-muted ml-2">~</span>
                <span className="text-foreground ml-2">./show_skills.sh</span>
                {currentLine === 1 && (
                  <span className="inline-block w-[0.6em] h-[1.1em] bg-primary ml-1 align-middle animate-blink" />
                )}
              </div>
            )}

            {/* Line 5: Skills with cycling */}
            {currentLine >= 1 && (
              <div
                className="fade-in"
                style={{ animationDelay: "800ms" }}
                onAnimationEnd={() => setShowSkills(true)}
              >
                <div className="flex items-center gap-3 flex-wrap">
                  <span className="text-foreground-muted">Specialties:</span>
                  {showSkills && (
                    <span className="text-primary font-medium transition-all duration-300">
                      {skills[currentSkillIndex]}
                    </span>
                  )}
                  {showSkills && (
                    <span className="inline-block w-[0.6em] h-[1.1em] bg-primary align-middle animate-blink" />
                  )}
                </div>
              </div>
            )}
          </div>
        </div>

        {/* Decorative glow effect */}
        <div
          className="absolute -inset-1 rounded-xl opacity-20 blur-xl -z-10 pointer-events-none"
          style={{
            background:
              "linear-gradient(135deg, var(--primary) 0%, transparent 50%, var(--primary) 100%)",
          }}
        />
      </div>

      {/* CSS for cursor blink animation */}
      <style>{`
        @keyframes blink {
          0%, 50% { opacity: 1; }
          51%, 100% { opacity: 0; }
        }
        .animate-blink {
          animation: blink 1s step-end infinite;
        }
      `}</style>
    </section>
  )
}

export default Hero
